




































































module.exports =
  name: 'work'
  data: ->
    featuredIndex: 0
    picIndex: 0


  computed:
    projects: -> return this.$store.state.projects
  methods:
    assetPath: (image)-> return require('@/assets/' + image)
    loadPicture: (index)-> this.picIndex = index

    moveFeaturedIndex: ->
      if @featuredIndex < @projects.length - 1
        @featuredIndex += 1
      else
        @featuredIndex = 0




